@import '/src/theme/_base';

.container {
  &__flexBox {
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    height: 100vh;
    padding: 16px;
  }

  &__title {
    margin: 46px 0;
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
    line-height: 30px; /* 125% */
    letter-spacing: -0.48px;
    text-align: center;
    color: #000;
  }

  &__iconsBox {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__arrowIcon {
    margin-top: 28px;
  }
}
