@import '/src/theme/_base';

.container {
  &__flexbox {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 95vh;
    padding: 16px;
  }

  &__header {
    margin-top: 20px;
    font-size: 32px;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: -0.02em;
    text-align: center;
    color: #000;
  }

  &__description {
    margin: 25px 0 40px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
  }

  &__image {
    position: relative;
    width: 100%;
    margin: 0 auto;
  }

  &__imageOverlay {
    position: absolute;
    top: 32%;
    right: 8%;
    z-index: 2;
    width: 84%;
    height: 55%;
    background-color: #8eff5c;
    opacity: 0.3;
  }
}
